import { ValidationProvider, ValidationObserver } from 'vee-validate';
export default {
    layout: 'shop',
    components: {
        ValidationProvider, ValidationObserver
    },
    data() {
        return {
            rating: 0,
            commentData: {
                name: '',
                email: '',
                comment: ''
            }
        };
    },
    async asyncData({ redirect, params, $services }) {
        try {
            const slug = params.slug;
            const response = await $services.Product.show(slug);
            return { product: response.data };
        } catch (error) {
            redirect('/404');
        }
    },
    computed: {
        calculateSalePrice() {
            return this.product.price - (this.product.salePrice / 100) * this.product.price;
        }
    },
    methods: {
        async submit(payload) {
            let formData = {
                ...this.commentData,
                product_id: this.product.id,
                rate: this.rating
            };
            try {
                await this.$services.ProductReview.store(formData);
                this.commentData.name = '';
                this.commentData.email = '';
                this.commentData.comment = '';
                this.rating = 0;
                this.$nuxt.refresh();
            } catch (error) {
                console.log(error);
            }
        }
    }
};
